/* =================
VARIABLES #TODO: odpowiednio przeskalować, tak aby nie wylewał się obraz poza ekran
==================*/
$font:'Lato', sans-serif;
$grey:#FAFAFA;
$white:#FFFFFF;
$action-color:#8C00FF;
$black: #000000;
$red: #FC7171;
$dark-grey: #cececeb2;

$top-bar-height: 75px;
$bottom-bar-height: 50px;

/* =================
TYPOGRAPHY
==================*/
$secondaryText: 12px;
.view-header {
    align-self: flex-start;
    margin: 10px;
    // margin: clamp(25px, 1.2em, 60px);
}
.bold-header {
    text-align: center;
    padding-left: 1em;
    padding-right: 1em;
}
/* =================
GLOBALS & UTILITIES
(ie. centering a div)
==================*/

@mixin flexCenter($direction) {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: $direction;

}
@mixin genericShadow() {
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
}
@mixin actionTheme() {
    color: $white;
    background-color: $action-color;
}
.main {
    width: 100%;
    justify-content: center;
    display: flex;
    flex-direction: column;
    color: $black;
    background-color: $white;
  }
.wrapper {
    @include flexCenter(column);
}

@mixin fitInput {
    width: 80%;
    
}

.center-content {
    @include flexCenter(column)

}
.viewport-center-content {
    @include flexCenter(column);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 3;
    width: 100vw;
    height: 100vh;
    background-color: #424242b2;

}


/* =================
VIEWS
==================*/
@mixin view {
    @include flexCenter(column);
}
.Login-view {
    @include view;
    height: 100vh;
}
.Match-view {
    @include view;
    
    height: calc(100vh - $top-bar-height);
}
.Searching-view {
    @include view;
    height: 100vh;
}
.Chat-view {
    @include view;
    justify-content: space-between;
    height: 100vh;
}
.Chat-end-view {
    @include view;
    justify-content: space-between;
    height: 100vh;
}
.scanner {
    background-color: $grey;
    
}
.footer {
    
    @include flexCenter(row);
    width: 170px;
    font-size: $secondaryText;
    justify-self: flex-end;
    justify-content: space-between
    // bottom: 0px;
}
/* =================
TOP BAR & BOTTOM BAR
==================*/
.top-bar {
    @include flexCenter(row);
    justify-content: space-between;
    width: 100%;
    height: $top-bar-height;
    padding-left: 1em;
    padding-right: 1em;

}
.top-chat-bar {
    @include flexCenter(row);
    justify-content: space-between;
    width: 100%;
    height: $top-bar-height;
    padding-left: 1em;
    padding-right: 1em;
    background-color: $white;
}
.bottom-bar {
    @include flexCenter(column);
    @include genericShadow();
    min-height: $bottom-bar-height;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    justify-content: space-between;
    width: 100vw;
    padding-left: 1em;
    padding-right: 1em;
    position: absolute;
    z-index: 3;
    bottom: 0px;
    left: 0px;
    text-decoration: none;

}
.bottom-bar-list {
    @include flexCenter(column);
    gap: 0.2rem;
    padding: 0.5rem;    
}
.nav-link {
    text-decoration: none;
}
/* =================
BOXES & CONTAINERS
==================*/

.content-container {
    @include flexCenter(column);
    width: clamp(120px, 60vw, 350px);
    // border: 1px solid black;
    @include genericShadow();
    border-radius: 20px;
    padding: 1em;
}

.content-area {
    @include flexCenter(column);
    width: clamp(120px, 60vw, 350px);
    // border: 1px solid black;
    // @include genericShadow();
    // border-radius: 20px;
    text-align: center;
    padding: 1em;
}

.searching-button-panel {
    @include flexCenter(column);
    width: clamp(120px, 60vw, 350px);
}
.choice-button-container {
    @include flexCenter(row);
    gap: 1em;
    width: clamp(120px, 60vw, 350px);
}


.circles-container {
    @include flexCenter(column);
    overflow: auto;
    // width: clamp(120px, 60vw, 500px);
}
.chat-body {
    display: flex;
    flex-direction: column;
    max-height: calc(100% - $bottom-bar-height - $top-bar-height);
    // position: absolute;
    bottom: $bottom-bar-height;
    overflow-y: auto;
    margin-bottom: calc(0.8em + $bottom-bar-height);
    width: clamp(120px, 90vw, 500px);
}
/* =================
FORMS
==================*/
.input-chat {
    @include flexCenter(row);
    background-color: $action-color;
    input {
        width: clamp(120px, 60vw, 500px);
    }
}
.input-form {
    @include flexCenter(column);
    
}
.send-message {
    width: 100%;
    @include flexCenter(row)
}
.input-form-line {
    padding-top: 1rem;
    @include flexCenter(row);
    // justify-content: space-;
    width: 100%;

    input {
        @include fitInput();
        margin-left: clamp(0.2rem, 0.5rem, 0.5rem);
        
    }
    .loginError{
        animation: color-change 1.5s,shake 0.82s cubic-bezier(.36,.07,.19,.97) both;
        img{
          animation: color-change 1.5s;
        }
        input{
          color: #FFFFFF;
          animation: color-change 1.5s;
        }
        ::placeholder{
          color: $white;
        }
      }
      @keyframes color-change {
        0% { background-color: $white;}
        50% { background-color: $red;}
        100% { background-color: $white;}
      }
      @keyframes shake {
        10%, 90% {
          transform: translate3d(-1px, 0, 0);
        }
        
        20%, 80% {
          transform: translate3d(2px, 0, 0);
        }
      
        30%, 50%, 70% {
          transform: translate3d(-4px, 0, 0);
        }
      
        40%, 60% {
          transform: translate3d(4px, 0, 0);
        }
      }
      

}
.join-circle-input-form-line{
    // width: 80%;
    @include flexCenter(row);
    margin-bottom: 1em;
    border: none;
}
.join-circle-input{
        border-bottom-left-radius: 0.5em;
        border-top-left-radius: 0.5em;
        width: 60%;
        
    }


/* =================
BUTTONS
==================*/
@mixin action-button {
    
    @include genericShadow();
    @include actionTheme();
    margin-top: 1rem;
    padding: 0.6em;
    border: none;
    border-radius: 30px;
}
.login-action-button {
    @include action-button();
    align-self: flex-end;
}
.chat-action-button {
    @include action-button();
    padding: 1.2rem;
    // align-self: flex-end;
}
.join-circle-button {
    border-top-right-radius: 0.5em;
    border-bottom-right-radius: 0.5em;
    background-color: $action-color;
}
.show-bottombar-button {
    @include action-button();
    border-radius: 30px;
}
.searching-button {
    @include action-button();
    margin-top: 0.6rem;
    border-radius: 30px;
}
.leave-circle-button {
    @include action-button();
}
.action-button {
    @include action-button();
}
/* =================
POP-UPS & INFO CLOUDS
==================*/
.info-cloud {
    @include flexCenter(column);
    padding: 1em;
    width: calc(100% - 1em);
    position: absolute;
    top: 0.5em;
    left: 0.5em;
    z-index: 2;
    border-radius: 5px;
    
    background-color: lightgreen;
    opacity: 95%;
    font-size: 1.4em;
}
.user-profile {
    @include flexCenter(column);
    // position: absolute;
    // top: 0.5em;
    // left: 0.5em;
    // z-index: 3;
    border-radius: 20px;
    height: 80vh;
    width: clamp(120px, 82vw, 500px);
    background-color: $grey;
    // opacity: 100%;
    
    // opacity: 60%;
    
}
.pop-up-base {
    @include flexCenter(column);
    border-radius: 20px;
    background-color: $grey;
    height: 80vh;
    width: clamp(120px, 82vw, 500px);

}
.pop-up-container {
    @include flexCenter(column);
}
/* =================
ICONS & IMAGES
==================*/
@mixin flippedImage{
    transform: rotateX(180deg);
}
@mixin panelArrow {
    margin-top: 0.7em;
}
.flipped-arrow{
    @include flippedImage();
    @include panelArrow();
}
.normal-arrow{
    @include panelArrow();
}
.profile-avatar{
    border-radius: 50%;
    width: 100px;
    height: 100px;

}
.chat-icon {
    // height: clamp(5vw, 20px, 40px);
    // width: clamp(5vw, 20px, 40px);
    max-height: 35px;
    max-width: 35px;
    max-height: 25px;
    max-width: 25px;
}
/* =================
MESSAGES 
==================*/
@mixin message {
    font-weight: bold;
    border-radius: 10px;
    margin-top: 0.6em;
    padding: 0.4em;
    width: fit-content;
    max-width: 70%;
    
}
.message-body-my{
    // @include message;
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: flex-end
}
.message-body-enemy{
    // @include message;
    display: flex;
    width: 100%;
    flex-direction: row;
    
}
.enemy-message{
    @include message;
    background-color: $dark-grey;
    border-top-left-radius: 0px;
}
.my-message{
    @include message;
    color: $white;
    border-top-right-radius: 0px;
    background-color: $action-color;
    justify-self: flex-end;
}
/* =================
ANIMATIONS
==================*/
@keyframes spin {
  
    0% {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
  
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
    
  }
.elipse {
    animation-name: spin;
    animation-duration: 2s;
    animation-iteration-count: infinite;
}
